<template>
  <div class="card">
    <div class="card-header" v-if="$slots.header && !$slots.default">
      <slot name="header"></slot>
    </div>
    <div class="card-body" v-if="$slots.body && !$slots.default">
      <slot name="body"></slot>
    </div>
    <div class="card-footer" v-if="$slots.footer && !$slots.default">
      <slot name="footer"></slot>
    </div>
    <slot v-if="$slots.default"></slot>
  </div>
</template>

<script>
// TO - DO all this shit with slots is quite restrictive, can't use custom classes on them
export default {
  name: 'card'
}
</script>



