import Vue from 'vue'
import Inventory from './inventory'
import Store from './store'

const Extensions = [ // Order matters for plugin extension
  Inventory,
  Store
];

export default function (routes) {
  routes = routes || []
  let extendables = []

  for (var i = 0; i < Extensions.length; i++) {
    // if (typeof Extensions[i].extend === 'function') extendables.push(Extensions[i].extend)
    if (Extensions[i].routes) routes.push(...Extensions[i].routes)
    Vue.use(Extensions[i])
  }

  /*
  for (var i = 0; i < extendables.length; i++) {
    extendables[i](routes) // (TO-DO use an index somehow to avoid a new iteration over routes)
  }
  */

  return routes
}



