// WIP - NOTETOSELF - TODO - Clean settings object from obsolete or corrupted properties
// Refactor this
import _ from 'lodash'

const settings = {
  "version": "0.1.0",
  "layout": {
    "theme": "default",
    "compactSidebar": true
  }
}

const updateSettings = function (path, value, localSettings) {
    localSettings = _.set(localSettings, path, value)
    window.localStorage.vueBoard = JSON.stringify(localSettings)
    return localSettings
}

// NOTETOSELF : Funcion should do only 1 thing, not update and initialize - REFACTOR
export default function (path, value, localSettings) {
  if (path && value && localSettings) {
    return updateSettings(path, value, localSettings)
  }

  localSettings = window.localStorage.vueBoard

  if (localSettings) {
    try {
      localSettings = JSON.parse(localSettings)
      localSettings = _.defaultsDeep(localSettings, settings)
    } catch (err) {
      localSettings = settings
    }
    window.localStorage.vueBoard = JSON.stringify(localSettings)
  } else {
    window.localStorage.vueBoard = JSON.stringify(settings)
  }

  return localSettings
}



