<template>
  <div>
    <div class="col-xs-12">
      <div class="app-heading clearfix">
        <div class="pull-left">
          <div class="app-title">
            <div class="title">Producto: <span class="highlight" v-if="item.name">{{ item.name }}</span></div>
            <!--<div class="description">102 result was found.</div>-->
          </div>
        </div>
      </div>
    </div>
    <tabs ref="tabs" type="card" class="form col-xs-12">
      <tab-pane icon="fa fa-facebook" label="set it off" sub-label="set it on">

        <template slot="label">
          <i class="icon fa fa-check-square fa-lg"></i>
          <span>Información</span> <br>
          <small>y detalles</small>
        </template>

        <v-section title="Detalles" icon="fa fa-info">
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <v-input label="Nombre" v-model="item.name"></v-input>
            </div>
            <div class="form-group">
              <v-input label="Descripción" v-model="item.description" type="textarea"></v-input>
            </div>
          </div>
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <label for="" class="control-label">Categoría</label>
              <v-multiselect v-model="item.category"
                :options="categories"
                :show-labels="false"
                :searchable="false"
                placeholder="Seleccionar">
              </v-multiselect>
            </div>
            <div class="form-group row">
              <div class="col-xs-12 col-sm-4 form-group">
                <v-input label="Stock" v-model="item.qty"></v-input>
              </div>
              <div class="col-xs-12 col-sm-8 form-group">
                <label for="" class="control-label">Proveedor</label>
                <v-multiselect v-model="item.providers"
                  :searchable="false"
                  :options="providers"
                  :show-labels="false"
                  placeholder="Seleccionar">
                </v-multiselect>
              </div>
            </div>
          </div>
        </v-section>
      </tab-pane>
      <tab-pane label="set it off">

        <template slot="label">
          <i class="icon fa fa-check-square fa-lg"></i>
          <span>Valor</span> <br>
          <small>Precios y moneda</small>
        </template>

        <v-section title="Precios" icon="fa fa-dollar">
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <v-input label="Precio de costo" v-model="item.costPrice">
                <template slot="prepend">$</template>
              </v-input>
              <small class="help-block">Por unidad</small>
            </div>
            <div class="form-group">
              <v-input label="Precio público" v-model="item.publicPrice">
                <template slot="prepend">$</template>
              </v-input>
              <small class="help-block">Por unidad</small>
            </div>
            <div class="form-group">
              <v-input label="Precio mayorista" v-model="item.vendorPrice">
                <template slot="prepend">$</template>
              </v-input>
              <small class="help-block">Por unidad</small>
            </div>
          </div>
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <label for="" class="control-label">Moneda</label>
              <v-multiselect v-model="item.currency"
                :options="currencies"
                :searchable="false"
                :show-labels="false"
                placeholder="Seleccionar">
              </v-multiselect>
            </div>
            <div class="form-group">
              <v-input label="SKU" v-model="item.sku" :disabled="true"></v-input>
              <small class="help-block">Número de referencia (<i>{Nombre-Variante-#número}</i>)</small>
            </div>
            <div class="form-group">
              <v-checkbox label="Precios incluyen I.V.A" v-model="item.pricesWithVAT"></v-checkbox>
              <small class="help-block">De lo contrario, especifica el valor</small>
            </div>
            <div class="form-group" v-if="!item.pricesWithVAT">
              <label for="" class="control-label">I.V.A.</label>
              <v-multiselect v-model="item.VAT"
                :options="[0, 10, 22]"
                :show-labels="false"
                placeholder="Seleccionar">
                <template slot="noResult">Ingresa el valor de I.V.A.</template>
              </v-multiselect>
            </div>
          </div>
        </v-section>
      </tab-pane>
      <tab-pane>
        <template slot="label">
          <i class="icon fa fa-check-square fa-lg"></i>
          <span>Presentación</span> <br>
          <small>Atributos y variantes</small>
        </template>

        <v-section title="Presentación" icon="fa fa-cube">
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <img :src="item.images[0].src" class="img-responsive center-block" v-if="item.images">
            </div>
            <div class="form-group">
              <!-- thumbs -->
            </div>
          </div>
          <div class="col-sm-12 col-md-6">
            <div class="form-group">
              <div v-if="item.variants">
                <div class="panel panel-body panel-default" v-for="(variant, vIndex) in item.variants">
                  <v-input class="form-group" label="Variente" v-model="variant.name">
                    <v-button @click="removeVariant(vIndex)" slot="append" type="link" size="xs" class="no-margin no-padding">
                      <i class="fa fa-2x fa-trash text-danger"></i>
                    </v-button>
                  </v-input>
                  <div class="row">
                    <v-input class="col-xs-12 col-sm-4" label="Cantidad" v-model="variant.qty"></v-input>
                    <v-input class="col-xs-12 col-sm-4" label="SKU" v-model="variant.sku"></v-input>
                    <v-input class="col-xs-12 col-sm-4" label="Precio" v-model="variant.price"></v-input>
                  </div>
                </div>
              </div>
              <v-button size="xs" type="success"
                icon="fa fa-plus"
                @click="item.variants.push({name: '', sku: '', qty: 1, price: ''})"
                class="center-block">
                Añadir variante
              </v-button>
            </div>
            <!-- NOTETOSELF - TODO - AÑADIR CARACTERISTICA (ADD FEATURE - to list) -->
          </div>
        </v-section>
      </tab-pane>

      <div class="panel-footer clearfix">
        <div class="pull-left">
          <v-button type="primary" @click="prev" icon="fa fa-chevron-left">Atrás</v-button>
          <v-button type="primary" @click="next" icon="fa fa-chevron-right">Siguiente</v-button>
        </div>
        <div class="pull-right">
          <v-button @click.prevent="cancel">Cancelar</v-button>
          <v-button type="success" icon="fa fa-save" @click="save">Guardar</v-button>
        </div>
      </div>
    </tabs>
  </div>
</template>

<script>
export default {
  data () {
    return {
      item: {
        id:1000,
        name: '',
        description:'',
        category:"Papas",
        qty:1,
        provider:"Fulano",
        costPrice:null,
        publicPrice:null,
        vendorPrice:null,
        currency: "Peso",
        pricesWithVAT:null,
        images:[{"src":"https://placehold.it/600x600"}],
        variants:[],
        features:[],
        VAT:null
      },
      error: null,
      categories: ['Papas', 'Boñatos', 'Nabos'],
      providers: ['Fulano', 'Mengano', 'Sultano'],
      currencies: ['Peso', 'Dollar']
    }
  },
  computed: {
    inputs () {
      return this.item
    }
  },
  methods: {
    prev () {
      this.$refs.tabs.prev(true)
    },
    next () {
      this.$refs.tabs.next(true)
    },
    cancel () {
      this.$router.push({name: 'inventory'})
    },
    save () {
      this.$router.push({name: 'inventory'})
    },
    removeVariant (vIndex) {
      // NOTETOSELF - $dialog('You sure?').then()
      this.item.variants.splice(vIndex, 1)
    }
  }
}
</script>



