<template>
<div id="app" class="app app-default">
  <sidebar :is-open="openSidebar" @toggle="toggleSidebar" v-if="$root.isAuthenticated"></sidebar>

  <!--<script type="text/ng-template" id="sidebar-dropdown.tpl.html">
    <div class="dropdown-background">
      <div class="bg"></div>
    </div>
    <div class="dropdown-container">
      {!{list}!}
    </div>
  </script>-->
  <div class="app-container" :class="{
    '__sidebar': openSidebar,
    '__navbar': openNavbar,
    'app-full': fullAppMode}" v-if="$root.isAuthenticated">

    <navbar :is-open="openNavbar" @toggle="toggleNavbar" @toggleSidebar="toggleSidebar"></navbar>

    <!--<div class="btn-floating" id="help-actions">
      <div class="btn-bg"></div>
      <button type="button" class="btn btn-default btn-toggle" data-toggle="toggle" data-target="#help-actions">
        <i class="icon fa fa-plus"></i>
        <span class="help-text">Shortcut</span>
      </button>
      <div class="toggle-content">
        <ul class="actions">
          <li><a href="#">Website</a></li>
          <li><a href="#">Documentation</a></li>
          <li><a href="#">Issues</a></li>
          <li><a href="#">About</a></li>
        </ul>
      </div>
    </div>-->

    <!--<div class="container" style="margin-top: -30px;background: #FFF;padding-top: 5px;margin-bottom: 20px">
      <sub-nav class="col-xs-12" v-if="$route.meta.children"></sub-nav>
    </div>-->
    <router-view class="row router-view"></router-view>

    <footer class="app-footer panel-body" v-show="!fullAppMode">
      <div class="row">
        <div class="col-xs-12">
          <div class="footer-copyright">
            Copyright © 2017 SutilSoft.
          </div>
        </div>
      </div>
    </footer>
  </div>
  <div id="login" v-if="!$root.isAuthenticated">
    <div class="bg"></div>
    <div class="row card">
      <div class="col-xs-12 col-md-6">
        <div class="card-body text-center">
          <img src="/static/images/sutil.png" class="img-responsive center-block form-group">

            <v-button type="primary" icon="fa fa-facebook">Entra con facebook</v-button>
          <div class="row panel-body">
            <div class="col-xs-5 o-ers"></div>
            <div class="col-xs-2">o</div>
            <div class="col-xs-5 o-ers"></div>
          </div>
          <div class="form-group">
            <div class="form-group">
              <v-input v-model="$root.username" placeholder="Usuario">
                <template slot="prepend">
                  <i class="fa fa-user"></i>
                </template>
              </v-input>
            </div>
            <div class="form-group">
              <v-input type="password" v-model="$root.password" placeholder="Contraseña">
                <template slot="prepend">
                  <i class="fa fa-key"></i>
                </template>
              </v-input>
            </div>
            <div class="form-group">
              <v-button @click="$root.signIn" type="success" class="btn-block" icon="fa fa-sign-in">Iniciar sesión</v-button>
            </div>
          </div>
        </div>
        <div class="card-footer text-center">
          <small class="text-muted">Con la tecnología de <strong>SutilSoft</strong></small>
        </div>
      </div>
      <div class="col-xs-12 col-md-6 visible-md visible-lg">
        <div class="card-body text-center center-block">
          <br><br>
          <img src="/static/images/logo.png" alt="" class="img-responsive center-block">
          <h3 style="
              letter-spacing: -2px;
              vertical-align: middle;
          ">¡Bienvenido a Admiral!</h3>
          <hr>
          <p class="lead">
            El panel de administración en la nube de SutilSoft
          </p>
        </div>
      </div>
    </div>
  </div>
</div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      openSidebar: false,
      openNavbar: false,
      fullAppMode: false
    }
  },
  methods: {
    toggleSidebar (off) {
      this.openSidebar = off === false ? off : !this.openSidebar
    },
    toggleNavbar () {
      this.openNavbar = !this.openNavbar
    },
    toggleAppMode (on) {
      this.openSidebar = on ? on : !this.openSidebar
    }
  },
  mounted () {
    window.addEventListener('resize', e => {
      this.openSidebar = false
    })
  }
}
</script>

<style src="@/assets/sass/app.scss" lang="sass"></style>
<style lang="sass">
#login
  padding: 70px 0px
  & > div.bg
    background: url('http://i062.radikal.ru/1310/58/ac4eaa31efdf.jpg') #000000
    background-size: cover
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    -webkit-filter: blur(5px)
    &:after
      content: ''
      background: rgba(0, 49, 45, 0.75)
      width: 100%
      height: 100%
      position: absolute
  & > .row
    max-width: 800px
    margin: 0 auto
    background: url(http://i062.radikal.ru/1310/58/ac4eaa31efdf.jpg)
    background-size: cover
    display: -webkit-box
    position: relative
    box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.6)
    & > div
      background: rgba(255, 255, 255, 0.95)
    & > div + div
      display: flex
      color: #FFF
      background: rgba(0, 49, 45, 0.75)
    img
      max-width: 100px
  .o-ers
    border-bottom: 1px solid #e7edee
    padding: 0.5rem
</style>



