<template>
  <div>
    <div class="card card-mini">
      <tabs @tab-click="tabClick">
        <tab-pane route="store">
          <template slot="label">
            <i class="icon fa fa-dashboard"></i>
            <span>Inicio</span>
          </template>
        </tab-pane>
        <tab-pane route="store.create">
          <template slot="label">
            <i class="icon fa fa-shopping-bag"></i>
            <span>Ventas</span>
          </template>
        </tab-pane>
        <tab-pane>
          <template slot="label">
            <i class="icon fa fa-info"></i>
            <span>Reportes</span>
          </template>
        </tab-pane>
        <tab-pane>
          <template slot="label">
            <i class="icon fa fa-users"></i>
            <span>Clientes</span>
          </template>
        </tab-pane>
        <tab-pane>
          <template slot="label">
            <i class="icon fa fa-rotate-left"></i>
            <span>Devoluciones</span>
          </template>
        </tab-pane>
      </tabs>
    </div>

    <router-view></router-view>

  </div>
</template>

<script>
export default {
  methods: {
    tabClick (tab) {
      let route = tab.$el.attributes.route
      if (route && route.value) this.$router.push({name: route.value})
    }
  }
}
</script>



