<template>
<aside class="app-sidebar" id="sidebar" :class="{'active': isOpen}">
	<div class="sidebar-header">
		<a class="sidebar-brand" href="#"><span class="highlight">Admi</span>ral</a>
		<button type="button" class="sidebar-toggle" @click.stop.prevent="$emit('toggle')">
			<i class="fa fa-times"></i>
		</button>
	</div>
	<div class="sidebar-menu">
		<ul class="sidebar-nav">
			<li v-for="item in items" :class="{
        'dropdown': item.children,
        'open': item.name === openDropdown || $route.name.indexOf(item.name) >= 0,
        'active': $route.name.indexOf(item.name) >= 0}">
        <!-- NOTETOSELF : Register parent item route so it checks directly instead of using indexOf-->
				<a href="#" :class="{'dropdown-toggle': item.children}" @click.stop.prevent="itemClick(item, true)">
					<div class="icon" v-if="item.icon">
						<i :class="item.icon"></i>
					</div>
					<div class="title">{{item.label}}</div>
          <button class="btn btn-default btn-sm pull-right" v-if="item.children && isOpen" @click.stop.prevent="itemClick(item)"
          style="position:absolute;right:0">
            <i class="fa fa-chevron-down"></i>
          </button>
				</a>
				<div class="dropdown-menu" v-if="item.children">
					<ul>
						<li v-for="child in item.children" :class="child.type">
							<span v-if="child.type && child.type === 'heading'">
								<i v-if="child.icon" :class="child.icon"></i>
								<span>{{child.label}}</span>
							</span>
							<span v-else-if="child.type && child.type === 'separator'"></span>
							<a href="#" @click.stop.prevent="itemClick(child)" v-else>{{child.label}}</a>
						</li>
					</ul>
				</div>
			</li>
		</ul>
	</div>
	<div class="sidebar-footer">
		<ul class="menu">
			<li>
				<a href="/" @click.stop.prevent="settingsDialog = true">
					<i class="fa fa-cogs"></i>
				</a>
			</li>
		</ul>
    <v-dialog
      :show="settingsDialog"
      @close="settingsDialog = false"
      title="Configuración de la interfaz"
      size="sm">
      <template slot="body">
        <div class="form-group">
          <label>Estilo</label>
          <ul class="list-inline">
            <li>
              <div class="img-circle" style="width:45px;height:45px;background:purple"></div>
            </li>
            <li>
              <div class="img-circle" style="width:45px;height:45px;background:green"></div>
            </li>
            <li>
              <div class="img-circle" style="width:45px;height:45px;background:skyblue"></div>
            </li>
            <li>
              <div class="img-circle" style="width:45px;height:45px;background:orange"></div>
            </li>
          </ul>
        </div>
        <div class="form-group">
          <v-checkbox
            label="Panel lateral compacto" id="sidebar-layouter">
          </v-checkbox>
        </div>
      </template>
    </v-dialog>
	</div>
</aside>
</template>

<script>
import _ from 'lodash'

const setMenuItem = function (route, item, collection) {
  // check for nested index route
  if (!route.name) {
    let firstChild = route.children[0]
    if (firstChild && firstChild.path === '') {
      route.name = firstChild.name
    }
    else {
      return collection
    }
  }

  item = {
    name: route.name,
    icon: item.icon ? item.icon : 'fa fa-cube',
    label: item.label ? item.label : route.name,
    type: item.type
  }

  // move "last" element to "children" property in path
  let path = route.name.split('.')
  if (path.length > 1) {
    let last = path[path.length-1]
    if (last) {
      path[path.length-1] = 'children'
      path[path.length]   = last
    }
  }

  _.set(collection, path, item)

  return collection
}

const sidebarTree = function (collection, route) {

  if (route.menuItem) collection = setMenuItem(route, route.menuItem, collection)

  if (!route.children || !route.children.length) return collection

  for (var i = 0; i < route.children.length; i++) {
    let child = route.children[i],
    item = child.menuItem
    if (item) {
      collection = setMenuItem(child, item, collection)
    }

    if (child.children && child.children.length) {
      collection = sidebarTree(collection, child)
    }
  }

  return collection
}

export default {
	props: ['isOpen'],
  data () {
    return {
      openDropdown: false,
      settingsDialog: false,
      settings: {
        compactSidebar: true
      }
    }
  },
  computed: {
    items () {
      let children = []
      return this.$router.options.routes.reduce((collection, route) => {
        return sidebarTree(collection, route)
      }
      , {})
    }
  },
	methods: {
		itemClick (item, direct) {
			if (item.children && !direct) {
        this.openDropdown = item.name === this.openDropdown ? false : item.name
			} else {
        this.dropdownOpen = false // close dropdown
        this.$router.push({name: item.name}) // navigate
        this.$parent.toggleSidebar(false) // close sidebar
			}
		}
	}
}
</script>



