import Index from './Index.vue'
import Dashboard from './Dashboard'
import Register from './Register'

export default [
  {
    path: '/store',
    component: Index,
    menuItem: {
      label: 'Tienda',
      icon: 'fa fa-shopping-cart'
    },
    children: [
      {
        path: '',
        name: 'store',
        component: Dashboard
      },
      {
        path: 'sell',
        name: 'store.create',
        component: Register,
        menuItem: {
          label: 'Ventas',
          icon: 'fa fa-shopping-bag'
        }
      },
      {
        path: 'sell',
        name: 'store.insights',
        component: Register,
        menuItem: {
          label: 'Reportes',
          icon: 'fa fa-info'
        }
      },
      {
        path: 'sell',
        name: 'store.clients',
        component: Register,
        menuItem: {
          label: 'Clientes',
          icon: 'fa fa-users'
        }
      },
      {
        path: 'sell',
        name: 'store.return',
        component: Register,
        menuItem: {
          label: 'Devoluciones',
          icon: 'fa fa-rotate-left'
        }
      }
    ]
  },
  /*{
    path: '/inventory/create',
    name: 'inventory.create',
    component: Create,
    menuItem: {
      label: 'Nuevo producto',
      icon: 'fa fa-plus'
    }
  },
  {
    path: '/inventory/item/:id',
    name: 'inventory.show',
    component: Show
  }*/
]



