<template>
<div class="section">
  <div class="section-title" v-if="title">
    <i class="icon" :class="icon" v-if="icon"></i>
    {{ title }}
  </div>
  <slot name="title" v-else-if="$slot.title"></slot>
  <div class="section-body" :class="{'__indent': indent}">
    <slot></slot>
  </div>
</div>
</template>

<script>
export default {
  props: ['title', 'icon', 'indent'],
  name: 'v-section'
}
</script>



