<template>
  <button :disabled="disabled" class="btn"
    @click="handleClick"
    :type="nativeType"
    :class="[
      type ? 'btn-' + type : '',
      size ? 'btn-' + size : '',
      {'btn-disabled': disabled},
      {'btn-responsive': responsive}
    ]"
  >
    <i class="icon-loading" v-if="loading" @click="handleInnerClick"></i>
    <i class="icon" :class="icon" v-if="icon && !loading" @click="handleInnerClick"></i>
    <span v-if="$slots.default" @click="handleInnerClick" :class="{'hidden-xs': responsive}"><slot></slot></span>
  </button>
</template>

<script>
  export default {
    name: 'v-button',
    props: {
      type: {
        type: String,
        default: 'default'
      },
      size: String,
      icon: {
        type: String,
        default: ''
      },
      nativeType: {
        type: String,
        default: 'button'
      },
      disabled: Boolean,
      loading: Boolean,
      responsive: Boolean
    },
    methods: {
      handleClick(evt) {
        this.$emit('click', evt)
      },
      handleInnerClick (evt) {
        if (this.disabled) evt.stopPropagation()
      }
    }
  };
</script>



