<template>
  <transition :name="transitionName">
    <div class="tab-pane"
    :class="{'active': active}"
    v-show="active">
      <slot></slot>
    </div>
  </transition>
</template>

<script>
  export default {
    name: 'tabPane',
    props: {
      label: String,
      closable: Boolean,
      disabled: Boolean
    },
    data() {
      return {
        index: null,
        labelContent: null,
      };
    },
    computed: {
      isClosable () {
        return this.closable || this.$parent.closable;
      },
      active () { //sx
        return this.$parent.currentTab ?
          this.$parent.currentTab.index === this.index : false
      },
      transitionName () {
        switch (this.$parent.type) {
          default:
            return 'tabs'
        }
      }
    },
    created () {
      this.$parent.addTab(this);
    },
    destroyed () {
      this.$parent.removeTab(this);
    },
    watch: {
      label () {
        this.$parent.$forceUpdate();
      }
    }
  };
</script>



