import Vue from 'vue'
import VueRouter from 'vue-router'
import Installer from '@/extensions/installer'
import Dashboard from '@/components/Dashboard'

Vue.use(VueRouter)

const scrollBehavior = function (to, from, savedPosition) {
  console.log(to.$el)
  return { x: 0, y: 0 }
}

const router = new VueRouter({
  base: '/',
  scrollBehavior,
  routes: Installer([
    {
      path: '/',
      name: 'index',
      component: Dashboard,
      /*menuItem: {
        label: 'Inicio',
        icon: 'fa fa-tasks'
      }*/
    }
  ])
})

// Global middlewares
// router.beforeEach()

export default router



