<template>
<div :class="[groupSize, {'input-group': isGroup}]">
  <label :for="options.id" class="control-label" v-if="label">{{ label }}</label>
  <div v-if="$slots.prepend" class="input-group-addon">
    <slot name="prepend"></slot>
  </div>
  <textarea
    v-if="type === 'textarea'"
    :rows="rows"
    :id="options.id"
    :value="value"
    :placeholder="placeholder"
    :class="{'read-only': readOnly}"
    @input="handleInput"
    @focus="handleFocus"
    @blur="handleBlur"
    :disabled="disabled"
    class="form-control"
    ></textarea>
  <input
    v-else
    :type="type"
    :id="options.id"
    :value="value"
    :placeholder="placeholder"
    :class="[inputSize, {'read-only': readOnly}]"
    :maxLength="maxLength"
    @input="handleInput"
    @focus="handleFocus"
    @blur="handleBlur"
    :disabled="disabled"
    class="form-control" />
  <div v-if="$slots.append" class="input-group-addon">
    <slot name="append"></slot>
  </div>
</div>
</template>

<script>
// NOTETOSELF : TODO : Input mixin for common shit between checkboxes, radios, inputs and selects
// TO-DO v-model.lazy support
/*
  <v-input label="Something" v-model.lazy="test">
    <template slot="prepend">$</template>
    <v-button slot="append" icon="fa fa-search"></v-button>
  </v-input>
*/
export default {
  name: 'v-input',
  props: {
    type: {default: 'text'},
    label: String,
    size: String,
    placeholder: String,
    value: {required: true},
    icon: String, // unused
    rows: {default: 3},
    maxLength: Number,
    disabled: {
      type: Boolean,
      default:false
    },
    readOnly: {
      type: Boolean,
      default:false
    },
    options: {
      default () {
        return {
          id: 'input-'+this._uid
        }
      }
    }
  },
  computed: {
    isGroup () {
      return (this.$slots.prepend || this.$slots.append)
    },
    inputSize () {
      switch (this.size) {
        case 'xs':
        case 'sm':
        case 'lg':
        return 'input-'+this.size
        default: return
      }
    },
    groupSize () {
      switch (this.size) {
        case 'xs':
        case 'sm':
        case 'lg':
        return 'input-group-'+this.size
        default: return
      }
    }
  },
  methods: {
    handleInput (e) {
      this.$emit('input', e.target.value)
    },
    handleFocus (e) {
      this.$emit('focus', e)
    },
    handleBlur (e) {
      this.$emit('blur', e)
    }
  }
}
</script>

<style>
.input-group label {
  display: table-caption
}
.form-control.read-only {
  border-color: transparent;
  padding: 0;
}
</style>



