<template>
<transition name="modal">
  <div class="modal"
    @click.self="close"
    tabindex="-1" role="dialog" v-if="show">
    <div class="modal-dialog" :class="modalSize">
      <div class="modal-content" v-if="$slots.omni">
        <slot name="omni"></slot>
      </div>
      <div class="modal-content" v-else>
        <div class="modal-header">
          <button type="button" class="close" @click.prevent="close">
            <i class="fa fa-close"></i>
          </button>

          <slot name="title" v-if="$slots.title"></slot>
          <h4 class="modal-title" v-else>
            {{ title }}
          </h4>
        </div>

        <div class="modal-body">
          <slot name="body" v-if="$slots.body"></slot>
          <slot v-else></slot>
        </div>

        <div class="modal-footer" v-if="$slots.footer">
          <slot name="footer"></slot>
        </div>
      </div>
    </div>
  </div>
</transition>
</template>

<script>
// NOTETOSELF : Try multiple modals same page, and event listener stacks
export default {
  props: ['show', 'title', 'size'],
  name: 'v-dialog',
  computed: {
    modalSize () {
      switch (this.size) {
        case 'sm':
          return 'modal-sm'
        case 'lg':
          return 'modal-lg'
        default:
          return 'modal-md'
      }
    }
  },
  methods: {
    close () {
      this.$emit('close')
    },
    escapeModal (e) {
      if (this.show && e.keyCode == 27) {
        this.close()
      }
    }
  },
  updated () {
    if (this.show) {
      document.body.classList.add('modal-open')
    }
    else {
      document.body.classList.remove('modal-open')
    }
  },
  mounted () {
    document.addEventListener('keydown', this.escapeModal)
  },
  destroyed () {
    document.removeEventListener('keydown', this.escapeModal)
  }
}
</script>

<style lang="scss">
/*NOTETOSELF : Merge in dialog.sass */
$modal-border-radius: 4px;
.modal {
  background-color: rgba(24, 25, 45, 0.5);
  transition: opacity .3s ease;
  display: block;
  .modal-dialog {
    -webkit-backface-visibility: hidden;
    transition: all .3s ease;
    .modal-content {
      font-weight: 300;
      .modal-header {
        border-top-right-radius: $modal-border-radius;
        border-top-left-radius: $modal-border-radius;
        h4 {
          font-weight: 300
        }
      }
      .modal-footer {
        border-bottom-right-radius: $modal-border-radius;
        border-bottom-left-radius: $modal-border-radius;
      }
    }
  }
}
/* Vue transition */
.modal-enter {
  opacity: 0;
  .modal-dialog {
    opacity: 0;
    -webkit-transform: scale3d(1.1, 1.1, 1);
    transform: scale3d(1.1, 1.1, 1);
  }
}
.modal-leave-active {
  opacity: 0;
  .modal-dialog {
    opacity: 0;
    -webkit-transform: scale3d(0.9, 0.9, 1);
    transform: scale3d(0.9, 0.9, 1);
  }
}
</style>



