<template>
<div>
  <div class="col-xs-12">
    <div class="app-heading clearfix">
      <div class="pull-left">
        <div class="app-title">
          <div class="title">Productos</span></div>
          <div class="description">50 resultados.</div>
        </div>
      </div>
      <div class="pull-right">
        <v-button type="success" size="xs" icon="fa fa-plus" @click="$router.push({name:'inventory.create'})" responsive>Nuevo</v-button>
      </div>
    </div>
  </div>
  <div class="row">
    <div class="col-xs-12">
      <div class="card card-mini">
        <div class="card-header">
          <div class="card-title">
            <strong>Inventario</strong>
          </div>
          <ul class="card-action list-inline">
            <li>
              <a href="#" @click.stop.prevent="toggleSearch">
                <i aria-hidden="true" class="fa fa-lg fa-remove text-danger" v-if="searchForm"></i>
                <i aria-hidden="true" class="fa fa-lg fa-search" v-else></i>
              </a>
            </li>
          </ul>
        </div>
        <div class="card-body no-padding">
          <data-table ref="table"  class="table-responsive"
            :data="tableData"
            :columns="columns"
            @row-click="openMenu">
            <template slot="name" slot-scope="props">
              <strong style="white-space: nowrap">{{ props.row.name }}</strong>
            </template>
            <template slot="bulkActions" slot-scope="props">
              <div class="panel-body">
                <v-button type="success" size="xs">Nueva venta</v-button>
                <v-button type="warning" size="xs">Deshabilitar</v-button>
                <v-button type="danger" size="xs">Borrar</v-button>
              </div>
            </template>
          </data-table>
        </div>
      </div>
    </div>
  </div>
  <v-dialog size="sm" :show="viewRow" :title="viewRow.name" @close="viewRow = false" class="actions-modal">
    <template slot="omni">
      <div class="panel-body row">
        <div class="col-xs-6">
          <v-button @click="$router.push({name: 'inventory.show', params: {id: viewRow.id}})" type="success">
            <i class="fa fa-3x fa-eye form-group center-block"></i>
            Ver
          </v-button>
        </div>
        <div class="col-xs-6">
          <v-button type="danger">
            <i class="fa fa-3x fa-trash form-group center-block"></i>
            Borrar
          </v-button>
        </div>
      </div>
      <v-button @click="viewRow = false" class="btn-block">Cerrar</v-button>
    </template>
  </v-dialog>
</div>
</template>

<script>
import Vue from 'vue'
import DataTable from '@/elements/dataTable'
Vue.use(DataTable)

export default {
  name: 'listing',
  data () {
    return {
      columns: [{
          label: 'Producto',
          name: 'name',
        },
        {
          label: 'Categoría',
          name: 'category',
        },
        {
          label: 'Cantidad',
          name: 'qty',
          indexable: false
        },
        {
          label: 'Proveedor',
          name: 'provider',
        },
        {
          label: 'Precio costo',
          name: 'costPrice',
          indexable: false
        },
        {
          label: 'Precio público',
          name: 'publicPrice',
          indexable: false
        }
      ],
      searchForm: false,
      viewRow: false
    }
  },
  computed: {
    tableData () {
      return this.$root.dummyData.inventory
    }
  },
  methods: {
    table () {
      return this.$refs.table
    },
    toggleSearch () {
      this.searchForm = this.table().toggleSearch()
    },
    openMenu (item, index) {
      this.viewRow = item
    }
  }
}
</script>

<style lang="sass">
.actions-modal
  .btn
    width: 100%
    display: block
    min-height: 90px
</style>



