<template>
  <div :class="[
    {'card card-tab card-mini no-padding': type === 'card'}
  ]">
    <div :class="{'card-header': type === 'card'}">
      <ul class="nav nav-tabs" @click="handleTabClick">
        <li v-for="tab in tabs"
          :class="{'active': tab.active, 'disabled': tab.disabled}"
          @click.stop.prevent="handleTabClick(tab)">
          <component :is="tab.labelContent"></component>
        </li>
      </ul>
    </div>
    <div class="tab-content" :class="{'card-body': type === 'card'}">
        <slot></slot>
    </div>
  </div>
</template>

<script>

const labelContent = tab => {
  let content = tab.$slots.label ? [tab.$slots.label] : tab.label
  return { // functional vue component
    functional: true,
    render: (h, context) => h('a', {
      class: {
        disabled: tab.disabled
      },
      attrs: {
        href: '#',
        disabled: tab.disabled
      },
      on: {
        click (ev) {
          ev.preventDefault()
        }
      }
    }, content)
  }
}

export default {
  name: 'tabs',
  props: {
    type: {default: ''},
    state: {default: 0},
    rewind: {default: true}
  },
  data () {
    return {
      tabs: {},
      tabState: 0
    }
  },
  computed: {
    tabKeys () {
      return Object.keys(this.tabs)
    },
    tabCount () {
      return this.tabKeys.length
    },
    currentTab () {
      return this.tabs[this.tabState] || {}
    }
  },
  methods: {
    addTab (tab) {
      tab.index = this.tabCount
      tab.labelContent = labelContent(tab)
      this.$set(this.tabs, tab.index, tab)
    },
    removeTab (tab) {
      this.$delete(this.tabs, tab.index)
    },
    go (state, ignoreDisabled, scrollIntoView) {
      if (!ignoreDisabled && this.tabs[state].disabled) return
      // this.$emit('stateChange')
      this.tabState = state
      if (scrollIntoView && this.$el.scrollIntoView)
        this.$el.scrollIntoView({block: "start", inline: "nearest", behavior: "smooth"})
    },
    next (scrollIntoView) {
      if (this.tabCount-1 > this.tabState)
        this.go(this.tabState+1, false, scrollIntoView)
      else
        if (this.rewind) this.go(0)
    },
    prev (scrollIntoView) {
      if (this.tabState !== 0)
        this.go(this.tabState-1, false, scrollIntoView)
      else
        if (this.rewind) this.go(this.tabCount-1)
    },
    handleTabClick (tab) {
      if (tab.disabled || !tab.$el) return
      this.go(tab.index)
      this.$emit('tab-click', tab)
    }
  },
  created () {
    this.go(this.state, true)
  }
}
</script>

<style>
.nav-tabs {
  user-select: none
}
</style>



