<template>
<div class="row">
  <div class="col-xs-12 col-md-6">
    <card class="card-mini">
      <div class="card-header">
        <div class="card-title">
          <strong>Venta</strong>
        </div>
        <div class="card-actions">
          <a href="#" class="no-margin text-muted" @click.prevent="clearCart">
            <i class="fa fa-rotate-left"></i>
          </a>
        </div>
      </div>
      <div class="panel-body">
        <ul class="list-group no-margin">
          <li v-for="(item, index) in cart.items" :key="item.id" class="list-group-item clearfix"
            @click="incrementItem(item)">
            <div class="label label-success col-xs-2 col-sm-1" @click.self.stop="setQty(item)">{{ cart.quantities[item.id] }}</div>
            <div class="col-xs-6 col-sm-8"><span>{{ item.name }}</span></div>
            <div class="text-right col-xs-4 col-sm-3">
              <span class="badge badge-primary badge-icon">
                <span>{{ (item.publicPrice * cart.quantities[item.id]).toFixed(2) }}</span>
                <i class="fa fa-close" @click="removeFromCart(item, index)" style="background:red;color:#FFF"></i>
              </span>
            </div>
          </li>
        </ul>
      </div>
      <div class="card-footer row">
        <div class="col-xs-6">
          <v-input v-model.number="discount" placeholder="0%" class="no-margin">
            <template slot="prepend">Descuento</template>
          </v-input>
        </div>
        <div class="col-xs-6">
          <v-input v-model.number="extra" class="no-margin">
            <template slot="prepend">Extras</template>
          </v-input>
        </div>
      </div>
      <div class="card-footer">
        <h5>Sub total:
          <div class="pull-right">
            $ {{ subTotal }}
          </div>
        </h5>
      </div>
      <div class="card-footer">
        <h4>Total:
          <div class="pull-right">
            $ {{ total }}
          </div>
        </h4>
      </div>
      <div class="card-footer text-center">
        <v-button type="primary" class="btn-block no-margin" @click.prevent="payCart">Confirmar</v-button>
      </div>
    </card>
  </div>
  <div class="col-xs-12 col-md-6">
    <card>
      <div class="card-header no-padding">
        <input type="text" class="form-control" placeholder="Filtrar productos" v-model="query">
      </div>
      <ul class="list-group">
        <li class="list-group-item no-margin"
        v-for="item in products"
        :key="item.id"
        @click="addToCart(item)"
        :class="{'item-selected': cart.items[item.id]}">
          <strong>{{ item.name }}</strong>
          <span class="label label-success">$ {{ item.publicPrice }}</span>
          <span class="badge"
          :class="item.qty === 0 ? 'badge-danger' : (item.qty < 10 ? 'badge-warning' : 'badge-primary')">
          {{ item.qty - (cart.quantities[item.id] || 0) }}
        </span>
        </li>
      </ul>
    </card>
  </div>
  <v-dialog size="lg" :show="paymentDialog" @close="paymentDialog = false">
  <template slot="omni">
    <tabs ref="tabs" type="card">
      <tab-pane label="Forma de pago" icon="fa fa-home">
        <ul class="list-group">
          <li class="list-group-item" :class="{'item-selected': paymentMethod === 'cash'}" @click="paymentMethod = 'cash'">
            <i class="icon fa fa-money"></i> Efectivo
          </li>
          <transition name="zoom-in">
            <li class="list-group-item" v-if="paymentMethod === 'cash'">
              <v-input v-model.number="cash" placeholder="$">
                <template slot="prepend">Entrega</template>
              </v-input>
            </li>
          </transition>
          <li class="list-group-item" :class="{'item-selected': paymentMethod === 'card'}" @click="paymentMethod = 'card'">
            <i class="icon fa fa-credit-card"></i> Tarjeta de crédito
          </li>
          <transition name="zoom-in">
            <li class="list-group-item" v-if="paymentMethod === 'card'">
              <div class="panel panel-default panel-body">
                <div class="col-xs-12 col-sm-8 form-group">
                  <v-input v-model="cardNumber" :maxLength="19" size="sm" label="Número de tarjeta" placeholder="4444-4444-4444-4444"></v-input>
                </div>
                <div class="col-xs-12 col-sm-4 form-group">
                    <div class="clearfix">
                      <label>Validez</label>
                    </div>
                    <div class="col-xs-4">
                      <v-input v-model="card.month" size="sm" placeholder="10" class="row"></v-input>
                    </div>
                    <div class="col-xs-6 col-xs-offset-2">
                      <v-input v-model="card.year" size="sm" placeholder="2017" class="row"></v-input>
                    </div>
                </div>
                <div class="col-xs-12 col-sm-8 form-group">
                  <v-input v-model="card.name" size="sm" label="Nombre del propietario" placeholder="Juan Pérez"></v-input>
                </div>
                <div class="col-xs-12 col-sm-4 form-group">
                  <v-input v-model="card.code" size="sm" label="Código" placeholder="0165"></v-input>
                </div>
              </div>
            </li>
          </transition>
          <li class="list-group-item disabled" :class="{'item-selected': paymentMethod === 'paypal'}" @click="paymentMethod = 'cash'">
            <i class="icon fa fa-paypal"></i> PayPal
          </li>
        </ul>
        <v-button type="primary" class="center-block" @click.prevent="nextTab">Siguiente</v-button>
      </tab-pane>
        <tab-pane label="Factura" icon="fa fa-home">
          <div class="alert alert-success text-center">
            <i class="fa fa-3x fa-check"></i>
            <h3>¡Venta exitosa!</h3>
            <p v-if="cash">Vuelto: {{ Math.round(cash - total) }}</p>
            <hr>
            <v-button type="success" @click.prevent>Imprimir factura</v-button>
            <v-button type="primary" @click="reset">Volver a la tienda</v-button>
          </div>
        </tab-pane>
    </tabs>
  </template>
  </v-dialog>
</div>
</template>

<script>
// NOTETOSELF - TODO - Implement variants and variant quantyties
export default {
  data () {
    return {
      query: '',
      cart: {
        client: 'Cliente de mostrador',
        items: {},
        quantities: {}
      },
      cash: '',
      discount: '',
      extra: '',
      paymentDialog: false,
      paymentMethod: 'cash',
      card: {
        number: '',
        month: '',
        year: '',
        code: '',
        name: ''
      }
    }
  },
  computed: {
    products () {
      return this.$root.dummyData.inventory.filter((item, index) => {
        return ['name', 'category'].some((prop) => {
          return item[prop].indexOf(this.query) != -1
        })
      }).slice(0, 10)
    },
    subTotal () {
      let total = 0;
      if (this.cart.items) {

      }
      for (let item in this.cart.items) {
        item = this.cart.items[item]
        total += item.publicPrice * this.cart.quantities[item.id]
      }
      return total.toFixed(2)
    },
    total () {
      return Math.ceil(this.subTotal - this.discount + this.extra).toFixed(2)
    },
    cardNumber: {
      get () {
        return this.card.number.toString(10).replace(/(.{4})/g,"$1-")
      },
      set (n, e) {
        n = n.replace(/[\-&]+/g, '')
        if (n.length <= 12) {
          console.log(n, e)
          this.card.number = n
        }
      }
    }
  },
  methods: {
    addToCart (item) {
      if (this.cart.items[item.id])
        return this.$delete(this.cart.items, item.id)

      this.$set(this.cart.items, item.id, item)
      this.$set(this.cart.quantities, item.id, 1)
    },
    removeFromCart (item, index) {
      this.$delete(this.cart.items, item.id)
      this.$delete(this.cart.quantities, item.id)
    },
    incrementItem (item) {
      if (item.qty > this.cart.quantities[item.id])
        this.cart.quantities[item.id] += 1
    },
    clearCart () {
      this.cart.items = {}
      this.cart.quantities = {}
    },
    setQty (item) {
      let qty = window.prompt('Especifica la cantidad')
      if (qty && item.qty >= qty)
        this.cart.quantities[item.id] = parseInt(qty)
    },
    payCart () {
      this.paymentDialog = true
    },
    nextTab () {
      this.$refs.tabs.next()
    },
    reset () {
      this.query = ''
      this.clearCart()
      this.cash = ''
      this.discount = ''
      this.paymentDialog = false
      this.paymentMethod = 'cash'
      this.card = {
        number: '',
        month: '',
        year: '',
        code: '',
        name: ''
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.list-group-item {
  cursor: pointer;
  user-select: none;
  &.item-selected {
    border-color: #29c75f;
    background: #29c75f;
    color: #FFF;
  }
}

.zoom-in {
  position: relative;
  width: calc(100% / 2 - 1rem);
  display: inline-flex;
  flex-direction: column;
  justify-content: space-between;
  margin-left: 1rem;
  margin-top: 1rem;
  padding-top: 0.75rem;
  border-radius: 6px;
  background-color: white;
  box-shadow: 0 0 0 1px #c5d0d1;
  backface-visibility: hidden;
  transform-origin: 10% 50%;
  z-index: 1;

  @media (min-width: 800px) { width: calc(100% / 3 - 1rem) }

  &-move { transition: all 600ms ease-in-out 50ms }
  &-enter-active { transition: all 300ms ease-out }

  &-leave-active {
    transition: all 200ms ease-in;
    position: absolute;
    z-index: 0;
  }

  &-enter,
  &-leave-to { opacity: 0 }
  &-enter { transform: scale(0.9) }

  &__data { line-height: 1.3 }
  &__label { font-size: 0.8rem }
  &__rating { text-align: center }

  &__info {
    padding: 0 0.75rem;
    text-align: center;
  }

  &__logo {
    width: 3rem;
    height: 3rem;
    margin: 0 auto;
  }

  &__name {
    height: 2.5rem;
    margin: 0.75rem 0;
    font-size: 1.3rem;
    font-weight: 200;
    text-align: center;
  }

  &__slogan {
    height: 2rem;
    text-align: center;
    font-weight: 400;
    text-transform: capitalize;
  }

  &__details {
    display: flex;
    justify-content: space-between;
    margin-top: 1.5rem;
    padding: 0.5rem 0.75rem;
    background-color: rgba(#c5d0d1, 0.1);
    border-top: 1px solid #c5d0d1;
  }

  &__country:hover {
    text-decoration: underline;
    cursor: pointer;
  }
}
</style>



