<script>
import Chartist from 'chartist'

export default {
  data () {
    return {
      chartData: {
        linear: {
          labels: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
          datasets: [
            {
              label: 'GitHub Commits',
              backgroundColor: '#f87979',
              data: [40, 20, 12, 39, 10, 40, 39, 40, 40, 20, 12, 11]
            }
          ],
          options: {
            responsive:false,
            maintainAspectRatio: false,
            showArea: true,
            fullWidth: true,
            lineSmooth: false
          }
        }
      }
    }
  },
  methods: {
    tabClick (tab) {
      let route = tab.$el.attributes.route
      if (route && route.value) this.$router.push({name: route.value})
    }
  },
  mounted () { //this.$refs.some
    let data = {
      series: [5, 3, 4]
    };

    let sum = (a, b) => { return a + b };

    let chart = new Chartist.Pie('.ct-square', data, {
      labelInterpolationFnc: (value) => {
        return Math.round(value / data.series.reduce(sum) * 100) + '%';
      },
      fullWidth: true,
      width: '250px',
      height: '250px'
    });

  }
}
</script>



