import Vue from 'vue'
import router from './router'
import Settings from './settings'

// Components
import Layout from './components/Layout'
import Navbar from './components/navbar'
import Sidebar from './components/sidebar'
Vue.use(Navbar)
Vue.use(Sidebar)

// Elements
import {Dialog, Checkbox, Button, Input, Section, Tabs, /*Charts,*/ Card} from './elements'
Vue.use(Dialog)
Vue.use(Card)
// Vue.use(Charts)
Vue.use(Checkbox)
Vue.use(Button)
Vue.use(Section)
Vue.use(Tabs)
Vue.use(Input)

// Third party elements
import Multiselect from 'vue-multiselect'
Vue.component('v-multiselect', Multiselect)
require('vue-multiselect/dist/vue-multiselect.min.css')

var data = require('./data/data').default; // fake data

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  render: h => h(Layout),
  data: {
    localSettings: Settings(),
    dummyData: data,
    username: null,
    password: null,
    user: {
      name: null
    }
  },
  computed: {
    isAuthenticated () {
      let user = this.user.name || (this.localSettings &&
        this.localSettings.auth &&
        this.localSettings.auth.user.name)
      return user ? true : false
    }
  },
  methods: {
    updateSettings (path, value) {
      this.localSettings = Settings(path, value, this.localSettings)
    },
    signIn () {
      if (!this.username) return
      this.user = {
        name: this.username
      }
      //this.updateSettings('auth.user', this.user)
    },
    signOut () {
      this.user = {
        name: null
      }
      //this.updateSettings('auth.user', null)
    }
  }
})



